import React from 'react';
import { Link } from 'react-scroll';
import { useTheme, styled } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import CloseIcon from '@mui/icons-material/Close';
import IconButton from '@mui/material/IconButton';
import CardMedia from '@mui/material/CardMedia';

import newsimage1 from '../images/news/popupbanner.jpg';
import newsimage2 from '../images/news/mobile.jpg';

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
  '& .MuiDialog-paper': {
    backgroundColor: 'transparent',
    boxShadow: 'none',
    overflow: 'visible',
  },
  '& .MuiDialogContent-root': {
    padding: 0,
    border: 'none',
    overflow: 'hidden', // 
    position: 'relative', // 
    borderRadius: '12px',
  },
}));

// 
const Snowflakes = () => {
  return [...Array(20)].map((_, i) => {
    const size = Math.random() * 5 + 2 + 'px'; //  2-7px
    const left = Math.random() * 100 + '%';    // 
    const duration = Math.random() * 3 + 2 + 's'; //  2-5 
    const delay = Math.random() * 5 + 's';        // 
    
    return (
      <div
        key={i}
        className="snowflake"
        style={{
          width: size,
          height: size,
          left: left,
          animationDuration: duration,
          animationDelay: delay,
          filter: 'blur(1px)', // 
        }}
      />
    );
  });
};

const Header = () => {
  const [open, setOpen] = React.useState(true);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  const handleClose = () => setOpen(false);

  const currentImage = isMobile ? newsimage2 : newsimage1;

  return (
    <div id='main'>
      <BootstrapDialog maxWidth='md' open={open} onClose={handleClose}>
        {/*   */}
        <IconButton
          aria-label="close"
          onClick={handleClose}
          sx={{
            position: 'absolute',
            // 
            right: { xs: -5, sm: -15}, //  / 
            top: { xs: -5, sm: -15 }, // 
            zIndex: 10,

            // ---  ---
            backgroundColor: '#ff6600',
            color: '#fff',              
            borderRadius: '50%',        
            padding: '0px',            
            boxShadow: '0px 4px 10px rgba(0,0,0,0.3)',

            '&:hover': {
              backgroundColor: '#e65c00', // 
              transform: 'scale(1.1)',    //  Hover
              transition: 'all 0.2s ease-in-out',
            },
          }}
        >
          <CloseIcon sx={{ fontSize: { xs: '20px', sm: '28px' } }} />
        </IconButton>

        <DialogContent>
          {/*  */}
          <Snowflakes />
          
          <CardMedia
            sx={{
              maxWidth: isMobile ? '100%' : 950,
              width: '100%',
              height: 'auto',
              display: 'block',
            }}
            component="img"
            image={currentImage}
            alt="Banner 2026"
          />
        </DialogContent>
      </BootstrapDialog>

      {/*  */}
      <div className='pr-heading'>
        <h2>LOGINS MEDICAL</h2>
        <h1><span>CONFIDENCE</span> in MOTION</h1>
        <div className='header-btns'>
          <Link to='footer' smooth={true} duration={1000} className='header-btn'>CONTACT US</Link>
        </div>
      </div>
    </div>
  );
};

export default Header;