import React from 'react';
import limage1 from '../../images/p-loginsRisk/risk-cover.jpg';
import iimage1 from '../../images/p-loginsRisk/p-1.png';
import iimage2 from '../../images/p-loginsRisk/p-2.png';
import iimage3 from '../../images/p-loginsRisk/p-3.png';

import dimage1 from '../../images/p-loginsRisk/d-1.png';
import dimage2 from '../../images/p-loginsRisk/d-2.png';
import dimage3 from '../../images/p-loginsRisk/d-3.png';

import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import CardActionArea from '@mui/material/CardActionArea';

const LoginsRisk = () => {
    return (
        <div id='product-detail'>
            <h1> LOGINS RISK MANAGEMENT</h1>
            <Grid
                container
                spacing={2}
                sx={{ padding: { xs: 2, sm: 4 } }}
            >
                {/*  maxWidth  Box  Card  */}
                <Box sx={{
                    maxWidth: 800, //   800px 
                    width: '100%',  //  Box 
                    borderRadius: 1,
                    bgcolor: ''
                }}>
                    <Card sx={{ m: 0 }}> {/*  margin  Card  0  */}
                        <CardActionArea>
                            <Box sx={{
                                position: 'relative',
                                width: '100%',
                                paddingBottom: '56.25%', // 16:9 aspect ratio
                                height: 0,
                                overflow: 'hidden',
                            }}>
                                <iframe
                                    style={{
                                        position: 'absolute',
                                        top: 0,
                                        left: 0,
                                        width: '100%',
                                        height: '100%',
                                        border: 0,
                                    }}
                                    src="https://www.youtube.com/embed/tKuZRtosDTw?si=WZ5Jd-brRPyc5TPq"
                                    title="YouTube video player"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                    referrerPolicy="strict-origin-when-cross-origin"
                                    allowFullScreen
                                >
                                </iframe>
                            </Box>
                        </CardActionArea>
                    </Card>
                </Box>
                <Box sx={{

                    maxWidth: 800,
                    borderRadius: 1,
                    bgcolor: ''
                }}>
                    <CardContent>
                        <Typography fontFamily='prompt' gutterBottom variant='h6' color='#F17834' >
                            ()
                        </Typography>
                        <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                                 LAB
                             LAB     
                            

                        </Typography>
                    </CardContent>
                </Box>
            </Grid>

            <Grid container
                direction="row"
                sx={{
                    justifyContent: "center",
                    alignItems: "center",
                }}
            >
                <Box sx={{
                    maxWidth: '800',
                    borderRadius: 1,
                    bgcolor: ''
                }}
                >
                    <CardContent>
                        <Typography fontFamily='prompt' gutterBottom variant='h6' color='#F17834' >
                            
                        </Typography>
                        <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                               Logins Risk Management  
                               IR 
                            
                        </Typography>
                    </CardContent>
                </Box>
            </Grid>
            <Grid container
                direction="row"
                sx={{
                    display: 'flex',
                    justifyContent: 'center',
                    alignContent: 'center',

                }}
            >
                <div className='product-detail-box'>
                    <Card sx={{ m: 2 }}>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="180"
                                maxWidth="800"
                                image={iimage1}
                                alt=""
                            />
                            <CardContent>
                                <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                                    1. 
                                </Typography>
                                <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                                     Barcode 
                                    
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>

                </div>
                <div className='product-detail-box'>
                    <Card sx={{ m: 2 }}>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="180"
                                maxWidth="800"
                                image={iimage2}
                                alt=""
                            />
                            <CardContent>
                                <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                                    2. 
                                </Typography>
                                <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                                      
                                     Miss / Near Miss   
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>

                </div>
                <div className='product-detail-box'>
                    <Card sx={{ m: 2 }}>
                        <CardActionArea>
                            <CardMedia
                                component="img"
                                height="180"
                                maxWidth="800"
                                image={iimage3}
                                alt=""
                            />
                            <CardContent>
                                <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                                    3. 
                                </Typography>
                                <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                                     /   
                                     IR 
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                    </Card>
                </div>
                <CardContent>
                    <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                        
                    </Typography>
                </CardContent>
                <CardActionArea>
                    <CardMedia
                        component="img"
                        height="202"

                        image={dimage1}
                        alt="CompanyProfile"
                    />
                </CardActionArea>
                <CardContent>
                    <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                        
                    </Typography>
                </CardContent>
                <CardActionArea>
                    <CardMedia
                        component="img"
                        height="685"

                        image={dimage2}
                        alt="CompanyProfile"
                    />
                </CardActionArea>
                <CardContent>
                    <Typography fontFamily='prompt' gutterBottom variant='body2' color='#F17834' >
                         IR
                    </Typography>
                </CardContent>
                <CardActionArea>
                    <CardMedia
                        component="img"
                        height="1139"

                        image={dimage3}
                        alt="CompanyProfile"
                    />
                </CardActionArea>
            </Grid>
        </div>
    )
}

export default LoginsRisk;