import React from 'react';
import Typography from '@mui/material/Typography';
import { Box, Container } from '@mui/material'; 
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';

const Offer = () => {
    return (
        // 1. div :  alignItems: 'center' 
        <div 
            id='presentaion' 
            style={{ 
                width: '100%', 
                minHeight: '100vh', 
                display: 'flex', 
                alignItems: 'center', //  
                justifyContent: 'center' 
            }}
        >
            <Container 
                maxWidth="lg" 
                sx={{ 
                    textAlign: 'center',
                    //   mt: { xs: '150px', sm: 0, md: 0 },  ( mt  Box )
                    //   minHeight 
                }}
            >
                {/* 2. Box :  Margin Top  Mobile  Navbar */}
                <Box
                    sx={{
                        fontFamily: 'Prompt, sans-serif', 
                        width: { xs: '90%', sm: '80%', md: '600px' }, 
                        maxWidth: '600px', 
                        mx: 'auto', 
                        p: { xs: 2, sm: 3, md: 0 }, 
                        position: 'relative', 
                        zIndex: 2,

                        // ***  :  Margin Top  Box  ***
                        //  150px ( 180px) 
                        mt: { xs: '180px', sm: 0, md: 0 }, 
                    }}
                >
                    {/* ---  (THE NEW SYSTEM AI COLLETION) --- */}
                    <Typography
                        fontFamily='Prompt, sans-serif'
                        fontWeight='700' 
                        gutterBottom
                        variant='h2'
                        sx={{
                            fontSize: { xs: '2.2rem', sm: '3rem', md: '4rem', lg: '5rem' }, 
                            lineHeight: { xs: 1.1, md: 1.2 }, 
                            mb: { xs: 2, md: 3 },
                            color: 'white',
                        }}
                    >
                        <span style={{ color: 'white' }}>THE </span>
                        <span style={{ color: 'orange' }}>NEW </span>
                        <span style={{ color: 'white' }}>SYSTEM </span>
                        <span style={{ color: 'orange' }}> AI </span> <br />
                        <span style={{ color: 'white' }}>COLLELATION</span>
                    </Typography>

                    {/* ---  (Paragraph) --- */}
                    <Typography
                        fontFamily='Prompt, sans-serif'
                        fontWeight='300'
                        gutterBottom
                        component='p' 
                        variant='body1' 
                        color='#fff'
                        display='block'
                        align='justify'
                        sx={{
                            fontSize: { xs: '0.85rem', sm: '0.9rem', md: '1rem' },
                            lineHeight: { xs: 1.6, md: 1.8 }, 
                            textIndent: { xs: '0em', md: '2em' },
                            mt: 0,
                            mb: '1em',
                        }}
                    >
                            
                        <span style={{ color: 'orange' }}> Correlation Lab System</span> 
                          
                          
                         
                        
                         
                         
                         Machine Learning  AI 
                          AI  Machine Learning 
                           
                    </Typography>

                    {/* ---  (Stack) --- */}
                    <Stack
                        spacing={2}
                        direction="row"
                        justifyContent="center"
                        alignItems="center"
                    >
                        <Button
                            variant="contained"
                            size="large"
                            sx={{
                                fontFamily: 'Prompt, sans-serif',
                                backgroundColor: '#F17834',
                                color: '#fff',
                                '&:hover': {
                                    backgroundColor: 'transparent',
                                    color: '#F17834',
                                    border: '1px solid #F17834',
                                },
                            }}
                        >
                            SOON
                        </Button>
                    </Stack>
                </Box>
            </Container>
        </div>
    );
}

export default Offer;