import React, { useState } from 'react';

// --- 1. Import Images ( Path ) ---
import limage1 from '../images/Loginslab.png';
import limage2 from '../images/bacteria.png';
import limage3 from '../images/central.png';
import limage4 from '../images/blood.png';
import limage5 from '../images/pcu.png';
import limage6 from '../images/outlab.png';
import limage7 from '../images/queue.png';
import limage8 from '../images/edoc.png';
import limage9 from '../images/risk.png';
import limage10 from '../images/healthConnect.png';
import limage11 from '../images/qc.png';
import limage12 from '../images/invplus.png';

// --- 2. Import Material UI Components ---
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions'; 
import DialogContent from '@mui/material/DialogContent';
import Typography from '@mui/material/Typography';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Box from '@mui/material/Box';
import CardMedia from '@mui/material/CardMedia';
import Card from '@mui/material/Card'; 
import CardContent from '@mui/material/CardContent'; 
import Grid from '@mui/material/Grid'; 

// --- 3. Import Product Detail Components ( Path ) ---
import LoginsLab from '../components/ProductsDetail/LoginsLab';
import LoginsBacteria from '../components/ProductsDetail/LoginsBacteria';
import LoginsCentralLab from '../components/ProductsDetail/LoginsCentralLab';
import LoginsBright from '../components/ProductsDetail/LoginsBright';
import LoginsPCU from '../components/ProductsDetail/LoginsPCU';
import LoginsOutlab from '../components/ProductsDetail/LoginsOutlab';
import LoginsRisk from '../components/ProductsDetail/LoginsRisk';
import HealthConnect from '../components/ProductsDetail/HealthConnect';
import LoginsQC from '../components/ProductsDetail/LoginsQC';
import LoginsQueue from '../components/ProductsDetail/LoginsQueue';
import LoginseDoc from '../components/ProductsDetail/LoginseDoc';
import LoginsInv from '../components/ProductsDetail/LoginsInv';


// --- 4. Product Data Array () ---
const productList = [
    {
        image: limage1,
        title: 'LogInS LAB',
        description: ' ""   ',
        component: LoginsLab,
    },
    {
        image: limage2,
        title: 'LogInS Bacteria',
        description: '   Preliminary Report, Additional Report, Final Report  Organism & Sensitivity',
        component: LoginsBacteria,
    },
    {
        image: limage3,
        title: 'LogInS Central LAB',
        description: ' ""   ',
        component: LoginsCentralLab,
    },
    {
        image: limage4,
        title: 'LogInS Bright',
        description: '      ',
        component: LoginsBright,
    },
    {
        image: limage5,
        title: 'LogInS PCU',
        description: ' LogInS PCU  Online  .  .. ',
        component: LoginsPCU,
    },
    {
        image: limage6,
        title: 'LogInS Out LAB',
        description: ' Out LAB  Online ',
        component: LoginsOutlab,
    },
    {
        image: limage7,
        title: 'LogInS Queue',
        description: 'LogInS Queue  ',
        component: LoginsQueue,
    },
    {
        image: limage8,
        title: 'LogInS E-Doc',
        description: '      ',
        component: LoginseDoc,
    },
    {
        image: limage9,
        title: 'LogInS Risk Management',
        description: '   ',
        component: LoginsRisk,
    },
    {
        image: limage10,
        title: 'LogInS Health Connect',
        description: ' HIS  ',
        component: HealthConnect,
    },
    {
        image: limage11,
        title: 'LogInS QC/ QC Online',
        description: ' Westgard Multirule  Lab Mean  Lab SD  IQC ',
        component: LoginsQC,
    },
    {
        image: limage12,
        title: 'LogInS Inv', 
        description: '',
        component: LoginsInv,
    },
];


const OurProduct = () => {
    const [open, setOpen] = useState(false);
    const [dialogContent, setDialogContent] = useState(null); 
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleOpenDialog = (Component) => {
        setDialogContent(<Component />);
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <Box id='rec-product' sx={{ 
            // **RESPONSIVE FIX:**  Padding 
            p: { xs: '30px 10px', md: '40px 6%' }, 
            textAlign: 'center', 
            backgroundColor: '#ffffff',
            width: '100%',
            fontFamily: 'Prompt, sans-serif', 
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            height: 'auto', 
        }}> 
            
            {/* --- :  Subtitle  Responsive Title --- */}
            <Typography variant="h2" component="h1" 
                sx={{ 
                    // **RESPONSIVE FIX:**  Margin Bottom 5  Font Size 
                    mb: 5, 
                    fontWeight: 800, 
                    color: '#2F365B',
                    textTransform: 'uppercase',
                    // **RESPONSIVE FIX:**  Font  Mobile/Tablet
                    fontSize: { xs: '2rem', sm: '2.5rem', md: '3rem' }, 
                    fontFamily: 'Prompt, sans-serif',
                }}
            >
                OUR PRODUCTS
            </Typography>
            {/* -------------------------------------------------------- */}
            
            {/* Grid Container: **alignItems="stretch"**  */}
            <Grid container spacing={4} justifyContent="center" alignItems="stretch" sx={{ width: '100%', maxWidth: '1400px' }}> 
                {productList.map((product, index) => (
                    
                    // Grid Item: **HARD FIX**  Responsive Breakpoints
                    <Grid item 
                          xs={12}    
                          sm={6}     // 2  Tablet
                          md={3}     // 4  Desktop
                          lg={3}     
                          key={product.title}
                          sx={{ 
                            display: 'flex', 
                            // **HARD FIX**  4 
                            flexBasis: { xs: '100%', sm: '50%', md: '25%' },
                            maxWidth: { xs: '100%', sm: '50%', md: '25%' },
                          }} 
                    >
                        
                        <Card 
                            elevation={3} 
                            sx={{ 
                                width: '100%', 
                                height: '100%', 
                                display: 'flex', 
                                flexDirection: 'column', 
                                boxSizing: 'border-box',
                                borderRadius: '10px', 
                                backgroundColor: '#ffffff', 
                                border: '1px solid #e5e5e5', 
                                cursor: 'pointer', 
                                transition: 'transform 0.3s, box-shadow 0.3s, border 0.3s',
                                '&:hover': {
                                    boxShadow: '2px 2px 12px rgba(255, 195, 54, 0.411)',
                                    border: '1px solid #F17834', 
                                    transform: 'translateY(-5px)', 
                                }
                            }}
                        >
                            {/* Card Media () */}
                            <Box sx={{ 
                                // **RESPONSIVE FIX:**  Padding  
                                p: { xs: 1.5, md: 2 }, pt: { xs: 3, md: 4 }, 
                                display: 'flex', justifyContent: 'center', alignItems: 'center', flex: 'none', 
                            }}>
                                <CardMedia
                                    component="img"
                                    sx={{ 
                                        objectFit: 'contain', 
                                        // **RESPONSIVE FIX:** 
                                        height: { xs: 70, md: 90 }, 
                                        width: { xs: 70, md: 90 } 
                                    }}
                                    image={product.image}
                                    alt={product.title}
                                />
                            </Box>
                            
                            {/* Card Content: **flexGrow: 1**  */}
                            <CardContent sx={{ 
                                p: 2, pt: 1, textAlign: 'center',
                                display: 'flex', flexDirection: 'column',
                                flexGrow: 1, 
                                minHeight: 0, 
                                minWidth: 0, 
                            }}>
                                <Typography 
                                    component="div"
                                    sx={{ 
                                        flex: 'none', 
                                        overflowWrap: 'break-word', 
                                        wordBreak: 'break-word', 
                                        color: '#F17834', 
                                        fontWeight: '600', 
                                        // **RESPONSIVE FIX:**  Font Title 
                                        fontSize: { xs: '1.1rem', md: '1.25rem' }, 
                                        mb: 1, 
                                    }} 
                                >
                                    {product.title}
                                </Typography>
                                
                                <Typography 
                                    fontFamily='Prompt' 
                                    fontWeight='300' 
                                    // **RESPONSIVE FIX:**  Font Description 
                                    fontSize={{ xs: '13px', md: '14px' }}
                                    color='#4a4a4a'
                                    sx={{ 
                                        flexGrow: 1, 
                                        lineHeight: 1.5,
                                        mb: 2, 
                                        display: '-webkit-box', 
                                        WebkitLineClamp: 5, 
                                        WebkitBoxOrient: 'vertical', 
                                        overflow: 'hidden', 
                                        textOverflow: 'ellipsis',
                                        overflowWrap: 'break-word', 
                                        wordBreak: 'break-word',
                                    }} 
                                >
                                    {product.description}
                                </Typography>
                            </CardContent>
                            
                            {/* Card Actions (): **mt: 'auto'** (Sticky Footer) */}
                            <Box sx={{ p: 2, pt: 0, textAlign: 'center', mt: 'auto', flex: 'none' }}>
                                <Button 
                                    variant="outlined" 
                                    onClick={() => handleOpenDialog(product.component)} 
                                    sx={{ 
                                        color: '#F17834', 
                                        borderColor: '#F17834',
                                        '&:hover': {
                                            borderColor: '#e06520',
                                            backgroundColor: '#fef3ec', 
                                        },
                                        fontWeight: 600,
                                        // **RESPONSIVE FIX:**  Font 
                                        fontSize: { xs: '12px', md: '13px' },
                                        padding: '6px 16px'
                                    }}
                                >
                                    OPEN MORE DETAIL
                                </Button>
                            </Box>
                        </Card>
                    </Grid>
                ))}
            </Grid>
            
            {/* Dialog Component (Modal ) */}
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
                sx={{ '& .MuiDialog-paper': { minWidth: { md: '60%', lg: '70%', xl: '70%' }, minHeight: '90%' } }}
            >
                <IconButton
                    aria-label="close"
                    onClick={handleClose}
                    sx={(theme) => ({
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        zIndex: 10,
                        color: theme.palette.grey[500],
                    })}
                >
                    <CloseIcon />
                </IconButton>
                
                <DialogContent dividers>
                    {dialogContent}
                </DialogContent>
                
                <DialogActions>
                    <Button onClick={handleClose}>
                        Close
                    </Button>
                </DialogActions>
            </Dialog>

        </Box>
    );
};

export default OurProduct;