import React from 'react';
import Card from '@mui/material/Card';
import Typography from '@mui/material/Typography';
import CardActionArea from '@mui/material/CardActionArea';
import CardMedia from '@mui/material/CardMedia';
import Tooltip from '@mui/material/Tooltip';
import Box from '@mui/material/Box'; 

// ***  path  ***
import timage1 from '../images/dev.png';
import timage2 from '../images/sa.png';
import timage3 from '../images/imp.png';
import timage4 from '../images/tech.png';

const teamsData = [
    {
        title: 'DEVELOPER',
        content: '     Logins Medical  Software  Software ',
        image: timage1,
        alt: 'Developer',
    },
    {
        title: 'SYSTEM ANALYSIS',
        content: '  Software      User  ',
        image: timage2,
        alt: 'System Analysis',
    },
    {
        title: 'IMPLEMENTOR',
        content: '    Implement   ',
        image: timage3,
        alt: 'Implementor',
    },
    {
        title: 'TECHNICIAN',
        content: '     ',
        image: timage4,
        alt: 'Technician',
    },
];

const Teams = () => {
    
    //   CardContent 
    const overlayContentStyle = {
        position: 'absolute',
        bottom: 0, // *** :  ***
        left: 0,
        right: 0,
        backgroundColor: '#2f365bc9', // 
        color: 'white',
        padding: '16px', 
        zIndex: 2, 
    };

    //  Blur  Dim Overlay
    const blurOverlayStyle = {
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backdropFilter: 'blur(0px)', 
        backgroundColor: 'rgba(88, 45, 32, 0.34)', 
        zIndex: 1,
    };
    
    return (
        <div id='ourteams'>
            <h1>OUR TEAMS</h1>

            {/* Flexbox Container:  Card  */}
            <div className='a-container' style={{ 
                display: 'flex', 
                flexWrap: 'wrap', 
                gap: '20px', 
                justifyContent: 'center',
                alignItems: 'stretch' 
            }}>
                
                {teamsData.map((team, index) => (
                    <div className='team-box' key={index} style={{ 
                        width: '300px', 
                        display: 'flex', 
                        flexDirection: 'column' 
                    }}>
                        <Card sx={{ 
                            maxWidth: 345, 
                            height: '100%' // Card  team-box
                        }}>
                            <Tooltip title={team.title}>
                                <CardActionArea sx={{ 
                                    position: 'relative', 
                                    height: '100%' // ActionArea  Card ()
                                }}> 
                                    
                                    {/* 1.  () */}
                                    <CardMedia
                                        component="img"
                                        height="100%" // *** :  ActionArea ***
                                        image={team.image}
                                        alt={team.alt}
                                        sx={{ 
                                            objectFit: 'cover', // 
                                            position: 'absolute', 
                                            top: 0 // *** :  ***
                                        }} 
                                    />
                                    
                                    {/* 2. Overlay  Blur  Dim */}
                                    <Box sx={blurOverlayStyle} />
                                    
                                    {/* 3.  () */}
                                    <Box sx={overlayContentStyle}>
                                        {/*  */}
                                        <Typography gutterBottom fontFamily='prompt' variant="h5" component="div" sx={{ color: 'white' }}>
                                            {team.title}
                                        </Typography>
                                        {/*  */}
                                        <Typography gutterBottom fontFamily='prompt' variant="body2" sx={{ color: 'rgba(255, 255, 255, 0.8)' }}>
                                            {team.content}
                                        </Typography>
                                    </Box>
                                    
                                </CardActionArea>
                            </Tooltip>
                        </Card>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Teams;