import React from 'react';
import { useTheme } from '@mui/material/styles';
import aboutimage from '../images/about.png';
import featureimage1 from '../images/feature/feature1.jpg';
import featureimage2 from '../images/feature/feature2.jpg';
import featureimage3 from '../images/feature/feature3.jpg';
import CardMedia from '@mui/material/CardMedia';
import Card from '@mui/material/Card';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import CardActionArea from '@mui/material/CardActionArea';
import CardContent from '@mui/material/CardContent';

const Feature = () => {
  const theme = useTheme();
  
  //  Grid item  layout 
  //  (xs)  12  ()
  // / (md)  6  ()
  const mainGridSize = { xs: 12, md: 6 };

  return (
    <Box id='features' sx={{ py: 4 }}> {/*  Box  div  padding  */}
      <Grid 
        container 
        spacing={4} //  Grid items
        justifyContent="center"
        alignItems="flex-start" // 
      >
        
        {/* ===  === */}
        <Grid item {...mainGridSize}>
          <Card elevation={4} sx={{ maxWidth: 800, mx: 'auto' }}> {/*  Card  */}
            <CardActionArea>
              <CardMedia
                component="img"
                //  height/maxWidth  hardcode  img 
                //  (minHeight)
                sx={{
                  width: '100%',
                  height: 'auto',
                  minHeight: 300, 
                  maxHeight: 515,
                  objectFit: 'cover'
                }}
                image={aboutimage}
                alt="CompanyProfile"
              />
            </CardActionArea>
          </Card>
        </Grid>
        
        {/* === / === */}
        <Grid item {...mainGridSize}>
          <Box sx={{ maxWidth: 800, mx: 'auto', p: { xs: 2, md: 0 } }}> {/*  padding  */}
            <CardContent sx={{ p: 0 }}> {/*  padding  CardContent */}
              <Typography
                fontFamily='prompt'
                fontWeight='600'
                gutterBottom 
                variant='h4'
                color='#F17834'
              >
                New FEATURE 2025
              </Typography>
              <Typography
                fontFamily='prompt'
                fontWeight='600'
                gutterBottom 
                variant='h2'
                color='#2F365B'
                sx={{ mb: 2 }} //  margin bottom
              >
                LogInS BACTERIA
              </Typography>
              <Typography 
                component="p" //  variant='caption'  p 
                fontFamily='prompt' 
                fontSize="16px"
                gutterBottom 
                variant='body1' //  body1  caption
                color='#1F233B' 
                sx={{ 
                  display: 'block',
                  textIndent: '2em', 
                  lineHeight: 1.8,
                  textAlign: 'justify' // 
                }}
              >
                   Barcode  Runing Number 
                 (Preliminary Report, Additional Report)  (Final Report),
                 Multi Result, Colony Count, Organism Isolate, MDR ()
                Sensitivity Test () /  Disk Diffusion Zone  MIC &nbsp; S ,SDD, I R 
                  Intrinsic Resistance   CLSI Update   Logins Bacteria
              </Typography>

              {/* ===  Feature  === */}
              <Grid
                container
                spacing={2} //  spacing 
                sx={{
                  justifyContent: 'center',
                  mt: 3 //  margin top
                }}
              >
                {/*  Array.map()   Grid item */}
                {[featureimage1, featureimage2, featureimage3].map((img, index) => (
                  <Grid item key={index} xs={12} sm={4} md={4} sx={{ maxWidth: 250 }}> {/*  responsive  */}
                    <CardMedia
                      component="img"
                      image={img}
                      alt={`Feature Image ${index + 1}`}
                      sx={{ 
                        height: 160, 
                        width: '100%', 
                        objectFit: 'cover',
                        borderRadius: 1 // 
                      }}
                    />
                  </Grid>
                ))}
              </Grid>
            </CardContent>
          </Box>
        </Grid>
        
      </Grid>
    </Box>
  );
};

export default Feature;