import React from 'react';
import oimage1 from '../../images/p-outlab/outlab-cover.jpg';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import CardActionArea from '@mui/material/CardActionArea';

const LoginsOutlab = () => {
  return (
    <div id='product-detail'>
      <h1> LOGINS OUTLAB</h1>
      <Grid container
        direction="row"
        sx={{
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        {/*  maxWidth  Box  Card  */}
                <Box sx={{
                    maxWidth: 800, //   800px 
                    width: '100%',  //  Box 
                    borderRadius: 1,
                    bgcolor: ''
                }}>
                    <Card sx={{ m: 0 }}> {/*  margin  Card  0  */}
                        <CardActionArea>
                            <Box sx={{
                                position: 'relative',
                                width: '100%',
                                paddingBottom: '56.25%', // 16:9 aspect ratio
                                height: 0,
                                overflow: 'hidden',
                            }}>
                                <iframe
                                    style={{
                                        position: 'absolute',
                                        top: 0,
                                        left: 0,
                                        width: '100%',
                                        height: '100%',
                                        border: 0,
                                    }}
                                    src="https://www.youtube.com/embed/esdRRH15oFg?si=L96ACSLaG6-VW2UL"
                                    title="YouTube video player"
                                    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                                    referrerPolicy="strict-origin-when-cross-origin"
                                    allowFullScreen
                                >
                                </iframe>
                            </Box>
                        </CardActionArea>
                    </Card>
                </Box>
        <Box sx={{

          maxWidth: 800,
          borderRadius: 1,
          bgcolor: ''
        }}>
          <CardContent>
            <Typography fontFamily='prompt' gutterBottom variant='h6' color='#F17834' >
              ( )
            </Typography>
            <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
               LogInSLAB    Outlab 
                 LAB   Barcode  
              Approve    Sacan    
               RIA, BRIA, TLC (), N Health 

            </Typography>
          </CardContent>
        </Box>
      </Grid>

      <Grid container
        direction="row"
        sx={{
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Box sx={{
          maxWidth: '800',
          borderRadius: 1,
          bgcolor: ''
        }}
        >
          <Card sx={{ m: 2 }}>
            <CardContent>
              <Typography fontFamily='prompt' gutterBottom variant='h6' color='#F17834' >
                
              </Typography>
              <Typography gutterBottom fontFamily='prompt' fontWeight='350'  fontSize="14px" sx={{ display: 'block' }}>
                   Lab  (Outlab)  
                   
                 TAT (turnaround time)  

              </Typography>
            </CardContent>
          </Card>
        </Box>
      </Grid>
    </div>
  )
}

export default LoginsOutlab;